html, body {
  margin: 0;
  padding: 0;
  height: 100%;
}

body {
  background-image: linear-gradient( lightgray, white 100px );
  background-repeat: no-repeat;
}

#root {
  height: 100%;
  font-size: 18px;
}
#app {
  height: 100%;
}

#sidebar {
  padding: 0 1em;
}

.nonprinting {
  display: none;
}

h1 {
  font-size: 200%;
}
h1, #loading h2 {
  text-align: center;
}
h1 img {
  height: 0.75em;
  width: 1em;
}
h1 button {
  vertical-align: middle;
}

#sidebar h2 {
  font-size: 100%;
}
#search-skip {
  font-size: 95%;
  font-weight: normal;
}
#sidebar p {
  font-size: 90%;
}

#main {
  min-height: 100%;
}

#main h2 {
  font-size: 95%;
  padding: 1em;
  margin: 0;
}
#main h3 {
  font-size: 90%;
}

#about {
  display: none;
}
#about.show {
  display: block;
}
#about h3 {
  display: block;
}

/* SEARCH */

#search-control {
  display: flex;
  padding: 0 1em;
}
#search-input {
  display: block;
  width: 100%;
}

#search-results {
  list-style: none;
  margin: 0;
  padding: 0 1em;
  background: white;
}
#search-results.hidden {
  display: none;
}
#search-results li {
  border: 1px solid lightgray;
}
#search-results a {
  display: block;
  padding: 5px;
  color: black;
  text-decoration: none;
}
#search-results a:hover {
  background: lightblue;
}
#search-results .result-desc {
  font-size: 90%;
}
#search-results .company-tags {
  display: block;
}
#search-results .company-tags li {
  margin: 0 0.5em 0 0;
}

/* LISTING */

#companies {
  padding-bottom: 2em;
}

.company-listing {
  padding: 0 1em;
}
.company-listing:target,
.showall .company-listing {
  padding: 1em;
  border-radius: 0.5em;
  background-color: #EEE;
  margin: 0 0 1px;
}
.company-listing:target {
  background-color: lightyellow;
}

h3 {
  display: inline-block;
  margin: 0;
  font-size: 90%;
  vertical-align: middle;
}
h3 a {
  text-decoration: none;
  color: #000;
}

.company-details {
  display: none;
}

.company-listing:target .company-details,
.showall .company-details {
  display: block;
}

.close-company {
  font-size: 0.75em;
}
.showall .close-company {
  display: none;
}

.company-brands {
  margin: 0.5em 0 0.5em 0.25em;
  padding: 0 0 0 0.5em;
  list-style: none;
  font-size: 80%;
  border-left: 2px solid black;
}

.company-source-section {
  margin: 0.5em 0 0;
  font-size: 70%;
}
.company-tags {
  font-size: 50%;
  text-transform: uppercase;
}

.company-source-section a {
  text-decoration: none;
}

.company-tags, .company-sources {
  display: inline-block;
  list-style: none;
  margin: 0;
  padding: 0;
}
.company-tags li, .company-sources li {
  display: inline-block;
  margin: 0 0 0 0.5em;
  padding: 0 5px;
  border-radius: 0.25em;
}

.company-tags li {
  background-color: maroon;
  color: white;
}
.company-tags li.PriorityBoycott {
  background-color: darkgreen;
}

.company-sources li {
  background-color: lightgray;
}


@media (min-width: 800px) {
  #root {
    max-width: 1024px;
    margin: 0 auto;
  }
  #app {
    display: flex;
    flex-direction: row-reverse;
    font-size: 16px;
  }
  #main {
    flex: 2;
  }
  #sidebar {
    flex: 1;
  }

  #about {
    display: block;
  }
  h1 button {
    display: none;
  }

  #search-skip {
    display: none;
  }

